#manage-feeds-component {
  .column {
    cursor: pointer;
  }
  .overlay {
    background: rgba(255,255,255,0.8);
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;

    .checkmark.icon {
      color: $green;
      font-size: 2em;
      width: 100%;
      height: 100%;
      line-height: 2em;
    }
  }
  text-align: center;
}